import Style from "./Intro.module.css";
import { imgToFontIcon } from "../../../../utils";
import PropTypes from "prop-types";
import React, { Component } from "react";

export class Intro extends Component {
  constructor(props) {
    super(props);
  }

  onDeleteBtnClick = event => {
    event.preventDefault();
    const introElemId = this.props.questionIntro.id;
    // this.props.toggleIntro();
    this.props.deleteQuestion(introElemId);
    setTimeout(() => {}, 10);
  };

  onConfigBtnClick = event => {
    event.preventDefault();
    this.props.toggleConfigModal();
    const introElemId = this.props.questionIntro.id;
    this.props.setCurrentEditor(introElemId);
  };

  getIcon = (iconName, iconStyle) => {
    switch (iconName) {
      case "id-card":
        return imgToFontIcon("idcard", Style.IconImageSvg);
        break;

      case "letter":
        return imgToFontIcon("document", Style.IconImageSvg);
        break;

      default:
        return imgToFontIcon(iconName, iconStyle);
        break;
    }
  };

  render() {
    return (
      <div className={Style.Intro}>
        {/* <a
          href="#"
          onClick={e => {
            this.onDeleteBtnClick(e);
          }}
          className={Style.removeIntroBtn}
        >
          <i className={`ion ion-ios-close-outline`}></i> Remove Intro
        </a> */}
        <div className={Style.controlCont}>
          <a
            className={Style.configElem}
            onClick={e => {
              this.onConfigBtnClick(e);
            }}
          >
            <i className="ion ion-ios-settings"></i>
          </a>
          <a
            className={Style.removeElem}
            onClick={e => {
              this.onDeleteBtnClick(e);
            }}
          >
            <i className="ion ion-ios-close-outline"></i>
          </a>
          <div className={Style.clearfix}></div>
        </div>
        <div className={Style.Info}>
          <h1 className="heading-primary">{this.props.formName}</h1>
          {/* <p className={Style.InfoText}>
        BT Account allow you to conduct transaction with pleasure, you can
        transfer unlimited amount of money to all banks in Nigeria
      </p> */}
        </div>
        <div className={Style.Instruction}>
          <span className={Style.InstructionWarning}>
            You'll need these to complete this form :
          </span>
          {/* <span className={Style.InstructionIcon}>
            <i className="far fa-hand-point-down" />
          </span> */}
        </div>
        <div className={Style.Requirements}>
          {this.props.questionIntro.children.map((child, index) => (
            <div className={Style.Requirement} key={index}>
              <div className={Style.Icon}>
                {this.getIcon(child.name, Style.IconImage)}
              </div>
              <div className={Style.RequirementTextWrapper}>
                <h3 className={Style.RequirementText}>{child.description}</h3>
              </div>
            </div>
          ))}
        </div>
        <div className={Style.Action}>
          <div className={Style.ActionBtnWrapper}>
            <button
              className={`btn--primary  ${Style.introGoBtn} btn btn--action`}
              onClick={this.props.toggleIntro}
            >
              I'm Ready
            </button>
          </div>
          <span className={Style.ActonInstruction}>
            Press <strong>Enter</strong>
          </span>
        </div>
      </div>
    );
  }
}

Intro.propTypes = {
  questionIntro: PropTypes.object.isRequired,
  toggleIntro: PropTypes.func.isRequired,
  formName: PropTypes.string.isRequired
};
